import * as yup from 'yup';
import { doesNeedOffer } from '../helpers';
import { TDocTypeSetting } from '../types';

export const validationEditSchema = yup.object().shape({
	files: yup.array().min(1, 'Files are required').required('Files are required'),
	docType: yup.object().shape({
		id: yup.string().required('Document type is required'),
	}),
	docStatus: yup.string().required('Document status is required'),
	docPermissions: yup
		.array()
		.min(1, 'Document permissions are required')
		.required('Document permissions are required'),
	offerId: yup.string().when('docType', {
		is: (docType: TDocTypeSetting) => doesNeedOffer(docType),
		then: yup.string().required('Offer is required'),
		otherwise: yup.string().nullable(),
	}),
});
